import ContentstackUIExtension from '@contentstack/ui-extensions-sdk';
import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    selectedAsset: null, // { url: 'https://placekitten.com/600/200', uid: '1234567890', title: 'Kitten.jpg' },
    focalPoint: {
      x: 0.5,
      y: 0.5,
    },
    searchResults: [
      { url: 'https://placekitten.com/300/200', uid: '1234567890', title: 'Kitten.jpg' },
      { url: 'https://placekitten.com/200/300', uid: '1234567890', title: 'Kitten.jpg' },
      { url: 'https://placekitten.com/500/500', uid: '1234567890', title: 'Kitten.jpg' },
      { url: 'https://placekitten.com/300/200', uid: '1234567890', title: 'Kitten.jpg' },
      { url: 'https://placekitten.com/200/300', uid: '1234567890', title: 'Kitten.jpg' },
      { url: 'https://placekitten.com/500/500', uid: '1234567890', title: 'Kitten.jpg' },
      { url: 'https://placekitten.com/300/200', uid: '1234567890', title: 'Kitten.jpg' },
      { url: 'https://placekitten.com/200/300', uid: '1234567890', title: 'Kitten.jpg' },
      { url: 'https://placekitten.com/500/500', uid: '1234567890', title: 'Kitten.jpg' },
      { url: 'https://placekitten.com/300/200', uid: '1234567890', title: 'Kitten.jpg' },
      { url: 'https://placekitten.com/200/300', uid: '1234567890', title: 'Kitten.jpg' },
      { url: 'https://placekitten.com/500/500', uid: '1234567890', title: 'Kitten.jpg' },
    ],
    searchText: '',
    extension: null,
  },
  mutations: {
    loadAllData(state, payload) {
      state.focalPoint = payload.focalPoint;
      state.selectedAsset = payload.selectedAsset;
      state.extension = payload.extension;
    },
    setFocalPoint(state, payload) {
      state.focalPoint = payload.focalPoint;
      this.dispatch('saveData');
    },
    setSelectedAsset(state, payload) {
      state.selectedAsset = payload.asset;
      state.focalPoint = {
        x: 0.5,
        y: 0.5,
      };
      this.dispatch('saveData');
    },
    setSearchText(state, payload) {
      state.searchText = payload.searchText;
    },
    setSearchResults(state, payload) {
      state.searchResults = payload.results;
    },
  },
  actions: {
    loadSavedValues({ commit }) {
      try {
        ContentstackUIExtension.init()
          .then((extension) => {
            const existingData = extension.field.getData();
            extension.window.updateHeight(600);
            extension.window.enableAutoResizing();
            commit({
              type: 'loadAllData',
              focalPoint: existingData.focal_point || { x: 0.5, y: 0.5 },
              selectedAsset: existingData.asset,
              extension,
            });
          });
      // eslint-disable-next-line no-empty
      } catch (e) {}
    },
    searchForAssets({ state, commit }, payload) {
      try {
        let assetQuery = state.extension.stack.Asset.Query();
        if (payload.searchText) {
          assetQuery = assetQuery.search(payload.searchText);
        }
        assetQuery.limit(10).find().then((results) => {
          commit({
            type: 'setSearchResults',
            results: results.assets,
          });
        });
      // eslint-disable-next-line no-empty
      } catch (e) {}
    },
    saveData({ state }) {
      try {
        state.extension.field.setData({
          asset: state.selectedAsset,
          focal_point: state.focalPoint,
        });
      // eslint-disable-next-line no-empty
      } catch (e) {}
    },
  },
});

export default store;
