<template>
  <section class="section" id="app">
    <ImagePanel v-if="showImage"/>
    <SearchPanel v-else/>
  </section>
</template>

<script>
import SearchPanel from './components/search/SearchPanel.vue';
import ImagePanel from './components/imageDetail/ImagePanel.vue';

export default {
  name: 'app',
  computed: {
    showImage() {
      return this.$store.state.selectedAsset;
    },
  },
  components: {
    SearchPanel,
    ImagePanel,
  },
  mounted() {
    this.$nextTick(() => {
      this.$store.dispatch('loadSavedValues');
    });
  },
};
</script>

