<template>
  <div class="media" id="tile" v-on:click="selectImage">
    <figure class="media-left">
      <p class="image is-64x64">
        <img id="thumbnail" v-bind:src=asset.url>
      </p>
    </figure>
    <div class="media-content">
      <div class="title is-6">{{ asset.title }} : {{ asset.uid }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    asset: Object,
  },
  methods: {
    selectImage() {
      this.$store.commit({
        type: 'setSelectedAsset',
        asset: this.asset,
      });
    },
  },
};
</script>

<style>

#tile {
  background-color: #ccc;
  padding: 10px;
  border-radius: 5px;
}

#thumbnail {
  object-fit: contain;
  height: 100%;
  width: 100%;
  border-radius: 5px;
  background-color: #aaa;
}
</style>
