<template>
  <div>
    <img
      id="selectedImage"
      :src="imageURL"
      @click="getClickLocation"
    />
    <canvas id="crosshair" :focalPoint="updateCrosshair"></canvas>
  </div>
</template>

<script>
const getClickLocationInDiv = (event, div) => ({
  x: Math.abs(event.clientX - div.x),
  y: Math.abs(event.clientY - div.y),
});

const getImgScale = img => Math.min(img.height / img.naturalHeight, img.width / img.naturalWidth);

const getScaledImgRectInDiv = img => ({
  x: Math.abs(img.naturalWidth * getImgScale(img) - img.width) / 2,
  y: Math.abs(img.naturalHeight * getImgScale(img) - img.height) / 2,
  width: img.naturalWidth * getImgScale(img),
  height: img.naturalHeight * getImgScale(img),
});

const clamp = (min, max) => v => Math.min(max, Math.max(min, v));
const clamp0to1 = clamp(0, 1);

const getClickLocationRelativeToImg = (event, img) => {
  const clickInDiv = getClickLocationInDiv(event, img);
  const imgRect = getScaledImgRectInDiv(img);
  return {
    x: clickInDiv.x - imgRect.x,
    y: clickInDiv.y - imgRect.y,
  };
};

const convertClickInImageToFocalPoint = (event, image) => {
  const imgRect = getScaledImgRectInDiv(image);

  const locationInImg = getClickLocationRelativeToImg(event, image);

  return {
    x: clamp0to1(locationInImg.x / imgRect.width),
    y: clamp0to1(locationInImg.y / imgRect.height),
  };
};

const convertFocalPointToDiv = (focalPoint, img) => {
  const imgRect = getScaledImgRectInDiv(img);

  return {
    x: imgRect.x + imgRect.width * focalPoint.x,
    y: imgRect.y + imgRect.height * focalPoint.y,
  };
};

const drawCrosshair = (focalPoint) => {
  const img = document.querySelector('#selectedImage');
  const c = document.querySelector('#crosshair');

  const crosshairLocation = convertFocalPointToDiv(focalPoint, img);

  const ctx = c.getContext('2d');
  ctx.clearRect(0, 0, c.width, c.height);
  ctx.beginPath();
  ctx.strokeStyle = '#f00';
  ctx.lineWidth = 3;
  ctx.rect(crosshairLocation.x - 15, crosshairLocation.y - 15, 30, 30);
  ctx.stroke();
};

export default {
  computed: {
    imageURL() {
      return this.$store.state.selectedAsset.url;
    },
    updateCrosshair() {
      return this.$store.state.focalPoint;
    },
  },
  methods: {
    getClickLocation(event) {
      const theImage = document.querySelector('#selectedImage');

      this.$store.commit({
        type: 'setFocalPoint',
        focalPoint: convertClickInImageToFocalPoint(event, theImage),
      });
    },
  },
  updated() {
    this.$nextTick(() => {
      drawCrosshair(this.$store.state.focalPoint);
    });
  },
  mounted() {
    this.$nextTick(() => {
      const canvas = document.querySelector('#crosshair');
      const img = document.querySelector('#selectedImage');
      canvas.width = img.clientWidth;
      canvas.height = img.clientHeight;
      canvas.top = img.clientLeft;
      canvas.left = img.clientTop;

      drawCrosshair(this.$store.state.focalPoint);
    });
  },
};
</script>

<style>

#selectedImage {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

#crosshair {
  left: 1.5rem;
  position: absolute;
  pointer-events: none;
}

</style>
