<template>
  <div class="tile is-ancestor" id="container">
    <div class="tile is-parent is-vertical">
      <div class="tile is-child">
        <SelectedImage id="bigImage"/>
      </div>
      <div class="tile is-child">
        <div class="title is-6">{{ asset.title }}: {{ asset.uid }}</div>
      </div>
      <div class="tile is-child">
        <div class="title is-6">Focal Point: { x: {{ focalPoint.x }}, y: {{ focalPoint.y }} }</div>
      </div>
      <button class="button is-danger" @click="clearSelectedImage">
        Select another image.
      </button>
    </div>
  </div>
</template>

<script>
import SelectedImage from './SelectedImage.vue';

export default {
  computed: {
    asset() {
      return this.$store.state.selectedAsset;
    },
    focalPoint() {
      const fp = this.$store.state.focalPoint;

      return {
        x: parseFloat(fp.x).toFixed(3),
        y: parseFloat(fp.y).toFixed(3),
      };
    },
  },
  components: {
    SelectedImage,
  },
  methods: {
    clearSelectedImage() {
      this.$store.commit({
        type: 'setSelectedAsset',
        asset: null,
      });
    },
  },
};
</script>

<style>

#container {
  height: 800px;
}

#bigImage {
  height: 600px;
  background-color: #888;
  border-radius: 8px;
  padding: 0%;
}

</style>
